@import "../SASS/Utils/variable";
@import "../SASS/Utils/mixin";

.carroussel {
    position: relative;
    display: flex;
    height: 415px;
    border-radius: 25px;
    background-position: center;
    background-size: cover;
    width: 95%;
    margin: 40px auto;

    @media screen and (max-width:480px) {
        height: 255px;
        margin: 20px auto;
        border-radius: 10px;


    }


    &__arrow {
        position: absolute;
        top: 50%;
        height: 15%;
        transform: translateY(-50%);
        cursor: pointer;
    }

    &__arrow:active {
        transform: translateY(-50%) scale(0.9);
    }

    &__arrow_right {
        right: 25px;
    }

    &__arrow_left {
        left: 25px;
    }
}

.slideCounter {
    position: absolute;
    bottom: 25px;
    left: 50%;
    transform: translateX(-50%);
    color: $color-secondary;
    font-weight: 500;
    font-size: clamp(14px, 2vw, 18px);


}